table {
  border: 0px !important;
}

tr {
  display: table-row;
  vertical-align: inherit;
  border-color: inherit;
  background-color: #FFF;
}

.breadcrumb {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  padding: .75rem 1rem;
  margin-bottom: 2rem;
  list-style: none;
  border-radius: .25rem;
  color: #262626;
}
.breadcrumb :hover {
  color: rgb(106, 183, 164);
}

.breadcrumb-item :focus-within {
  color: #ffffff;
}

.table td, .table th {
  border-top: none;
  vertical-align: none;
  display: wrap;
}

.svg-inline--fa.fa-w-16  {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-right: 5px;
}

.svg-inline--fa.fa-w-16:hover {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  margin-right: 5px;
}

td {
  position: static;
  outline: none;
  border-bottom: 1px dotted #666;
  height: 50px;
  overflow: hidden;
  color: #262626;
  font-size: 100%;
  font-weight: 300;
  text-decoration: none;
}

a {
  text-decoration: none;
}

.popover__title {
  font-size: 50px;
  line-height: 36px;
  text-decoration: none;
  color: white;
  text-align: center;
  padding: 15px 0;
}

.popover__wrapper {
  position: relative;
  margin-top: 1.5rem;
  display: inline-block;
}
.popover__content {
  opacity: 0;
  visibility: hidden;
  position: absolute;
  left: -150px;
  -webkit-transform: translate(0, 10px);
      -ms-transform: translate(0, 10px);
          transform: translate(0, 10px);
  background-color: #bfbfbf;
  padding: 1.5rem;
  -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
          box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  width: auto;
}
.popover__content:before {
  position: absolute;
  z-index: -1;
  content: "";
  right: calc(50% - 10px);
  top: -8px;
  border-style: solid;
  border-width: 0 10px 10px 10px;
  border-color: transparent transparent #bfbfbf transparent;
  -webkit-transition-duration: 0.3s;
       -o-transition-duration: 0.3s;
          transition-duration: 0.3s;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  -o-transition-property: transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
}
.popover_wrapper:hover .popover_content {
  z-index: 10;
  opacity: 1;
  visibility: visible;
  -webkit-transform: translate(0, -20px);
      -ms-transform: translate(0, -20px);
          transform: translate(0, -20px);
  -webkit-transition: all 0.5s cubic-bezier(0.75, -0.02, 0.2, 0.97);
  -o-transition: all 0.5s cubic-bezier(0.75, -0.02, 0.2, 0.97);
  transition: all 0.5s cubic-bezier(0.75, -0.02, 0.2, 0.97);
  border-radius:6px;
}
.popover__message {
  text-align: center;
}

.fa-customIcon{
  color: "white";
  font-size: "20pt";
}


